@charset "utf-8";
#header {
    padding: 0;
    height: 104px;
    width: 1000px;
    margin: 0 auto;
}
.header_inner {
    width: 1000px;
    margin: auto;
    position: relative;
    z-index: 31;
}
.header_inner .logo {
    width: 240px;
    margin: 0px;
    float: left;
    padding: 5px 0px 0px 0px;
    position: relative;
}
.header_inner .right {
    float:right;
    width:650px;
    padding-top:30px;
}
/* cartMain */
.cartMain {
    width: 1000px;
    margin: 0 auto;
}
.cartHead {
    padding: 0 0 10px 0;
}
.cartMy {
    float: left;
    font-family: 微软雅黑;
    font-size: 20px;
    line-height: 26px;
}
.areaShow {
    float: left;
}
.cartHead .toTxt {
    font-size: 14px;
}
/* 地区选择 */
#store-selector {
    float: left;
    height: 26px;
    margin-right: 6px;
    position: relative;
    z-index: 91;
}
#store-selector .text {
    width: auto;
    height: 25px;
    background: #FFFFFF;
    border: 1px solid #CECBCE;
    /* border-bottom: 0 none; */
    line-height: 23px;
    padding: 0 20px 0 4px;
    float: left;
    position: relative;
    top: 0;
    font-size: 12px;
    overflow: hidden;
    max-width: 500px;
    z-index: 1;
}
